export interface ErrorDetails {
  code: number;
  name: string;
  msg: string;
}

type COMMON_ERRORS = 'TransactionNotConfirmed' | 'BalancesNotExtractedProperly' | 'InsufficientSOL';

type JUPITER_V5_ERROR_TYPES = COMMON_ERRORS | (typeof IDL_V5.errors)[number]['name'];

export const COMMON_ERRORS = {
  AlreadyInUse: {
    code: 1,
    name: 'AlreadyInUsed',
    msg: 'Account already In Used',
  },
  InsufficientSOL: {
    code: 1,
    name: 'InsufficientSOL',
    msg: 'Insufficient SOL',
  },
  TransactionNotConfirmed: {
    code: 7000, // a code that is not in the list of errors
    name: 'TransactionNotConfirmed',
    msg: 'Transaction was not confirmed',
  },
  BalancesNotExtractedProperly: {
    code: 7001, // a code that is not in the list of errors
    name: 'BalancesNotExtractedProperly',
    msg: 'Balances cannot be extracted properly',
  },
};

export const JUPITER_V5_ERRORS: Record<JUPITER_V5_ERROR_TYPES, ErrorDetails> = Object.freeze({
  ...COMMON_ERRORS,
  ...IDL_V5.errors.reduce((accErrors, error) => {
    return { ...accErrors, [error.name]: error };
  }, {} as Record<(typeof IDL_V5.errors)[number]['name'], ErrorDetails>),
});
