import { TransactionError } from '@mercurial-finance/optimist';
import bs58 from 'bs58';
import {
  AccountKeysFromLookups,
  Blockhash,
  ConfirmedTransactionMeta,
  Connection,
  PublicKey,
  SendOptions,
  Signer,
  Transaction,
  TransactionSignature,
  VersionedMessage,
  VersionedTransaction,
  VersionedTransactionResponse,
} from '@solana/web3.js';
import { COMMON_ERRORS, JUPITER_PROGRAM_ID, WRAPPED_SOL_MINT } from '../constants';
import { wait } from './wait';
import { EventExtractor } from './eventExtractor';
import { BN, Event } from '@coral-xyz/anchor';

/** Should this be a method in web3.js? */
function getKeyMap(message: VersionedMessage, accountKeysFromLookups: AccountKeysFromLookups | undefined) {
  return message
    .getAccountKeys({ accountKeysFromLookups })
    .keySegments()
    .flat()
    .reduce((acc, key, index) => {
      acc.set(key.toBase58(), index);
      return acc;
    }, new Map<string, number>());
}

function diffTokenBalance(accountKeyIndex: number, meta: ConfirmedTransactionMeta): number | undefined {
  const postBalance = meta.postTokenBalances?.find(
    (postTokenBalance) => postTokenBalance.accountIndex === accountKeyIndex,
  )?.uiTokenAmount.amount;
  const preBalance = meta.preTokenBalances?.find((preTokenBalance) => preTokenBalance.accountIndex === accountKeyIndex)
    ?.uiTokenAmount.amount;

  // When token account is created it isn't present in preBalance
  if (!postBalance) return;
  return Math.abs(parseInt(postBalance) - (preBalance !== undefined ? parseInt(preBalance) : 0));
}

export function extractTokenBalanceChangeFromTransaction(
  meta: ConfirmedTransactionMeta,
  keyMap: Map<string, number>,
  tokenAccountAddress: PublicKey,
): number | undefined {
  if (!meta) return;
  const accountKeyIndex = keyMap.get(tokenAccountAddress.toBase58());
  if (accountKeyIndex === undefined) return;

  return diffTokenBalance(accountKeyIndex, meta);
}

/** Assumption: user is the signer so it can never be rolled */
export function extractSOLChangeFromTransaction(
  meta: ConfirmedTransactionMeta,
  keyMap: Map<string, number>,
  user: PublicKey,
): number {
  const accountKeyIndex = keyMap.get(user.toBase58());
  if (accountKeyIndex !== undefined) {
    return Math.abs(meta.postBalances[accountKeyIndex] - meta.preBalances[accountKeyIndex]);
  }

  return 0;
}

export function extractInputTokenBalanceChange(events: Event[], mint: PublicKey): number {
  let balanceChange: BN = new BN(0);
  events.forEach((event: any) => {
    console.log({
      mint: mint.toBase58(),
      inputMint: event.data.inputMint.toBase58(),
      inputAmoutn: event.data.inputAmount.toNumber(),
    });
    if (event.data.inputMint.equals(mint)) {
      balanceChange = balanceChange.add(event.data.inputAmount);
    }
  });

  return balanceChange.toNumber();
}

export function extractOutputTokenBalanceChange(events: Event[], mint: PublicKey): number {
  let balanceChange: BN = new BN(0);
  events.forEach((event: any) => {
    console.log({
      mint: mint.toBase58(),
      outputMint: event.data.outputMint.toBase58(),
      outputAmoutn: event.data.outputAmount.toNumber(),
    });
    if (event.data.outputMint.equals(mint)) {
      balanceChange = balanceChange.add(event.data.outputAmount);
    }
  });

  return balanceChange.toNumber();
}

export function getTokenBalanceChangesFromTransactionResponse({
  txid,
  inputMint,
  outputMint,
  user,
  sourceAddress,
  destinationAddress,
  transactionResponse,
  hasWrappedSOL,
}: {
  txid: TransactionSignature;
  inputMint: PublicKey;
  outputMint: PublicKey;
  user: PublicKey;
  sourceAddress: PublicKey;
  destinationAddress: PublicKey;
  transactionResponse: VersionedTransactionResponse | null;
  hasWrappedSOL: boolean;
}) {
  let sourceTokenBalanceChange: number | undefined;
  let destinationTokenBalanceChange: number | undefined;

  if (transactionResponse && transactionResponse.meta) {
    let { meta, transaction } = transactionResponse;
    const logMessages = meta.logMessages;

    if (logMessages) {
      const extractor = new EventExtractor();
      const events = extractor.getEvents(logMessages);
      const swapEvents = events.filter((event) => event.name === 'SwapEvent');

      sourceTokenBalanceChange = extractInputTokenBalanceChange(swapEvents, inputMint);
      destinationTokenBalanceChange = extractOutputTokenBalanceChange(swapEvents, outputMint);
    }

    // If we don't get the balance changes from the events, we extract them the old fashion way.
    if (!sourceTokenBalanceChange && !destinationTokenBalanceChange) {
      const keyMap = getKeyMap(transaction.message, meta.loadedAddresses);

      sourceTokenBalanceChange =
        inputMint.equals(WRAPPED_SOL_MINT) && !hasWrappedSOL
          ? extractSOLChangeFromTransaction(meta, keyMap, user)
          : extractTokenBalanceChangeFromTransaction(meta, keyMap, sourceAddress);
      destinationTokenBalanceChange =
        outputMint.equals(WRAPPED_SOL_MINT) && !hasWrappedSOL
          ? extractSOLChangeFromTransaction(meta, keyMap, user)
          : extractTokenBalanceChangeFromTransaction(meta, keyMap, destinationAddress);
    }
  }

  if (!(sourceTokenBalanceChange && destinationTokenBalanceChange)) {
    throw new TransactionError(
      'Cannot find source or destination token account balance change',
      txid,
      COMMON_ERRORS['BalancesNotExtractedProperly'].code,
      [JUPITER_PROGRAM_ID.toBase58()],
    );
  }

  return [sourceTokenBalanceChange, destinationTokenBalanceChange];
}

export function getUnixTs() {
  return new Date().getTime();
}

const SEND_OPTIONS: SendOptions = { skipPreflight: true, maxRetries: 2 };

type TransactionSenderAndConfirmationWaiterArgs = {
  connection: Connection;
  signedTransaction: Transaction | VersionedTransaction;
  timeout?: number;
  pollInterval?: number;
  sendInterval?: number;
  sendRetries?: number;
};

async function checkIsBlockhashValid({
  connection,
  blockhash,
}: {
  connection: Connection;
  blockhash: Blockhash;
}): Promise<boolean> {
  const args = connection._buildArgs([blockhash], 'processed');
  // @ts-ignore
  const response = await connection._rpcRequest('isBlockhashValid', args);
  const isBlockhashValid = response.result.value;
  return isBlockhashValid;
}

/**
 * awaits confirmation while resending the transaction periodically
 *
 * Our RPC node settings
 * solana_send_leader_count: 8
 * solana_send_retry_ms: 15000
 **/
export async function transactionSenderAndConfirmationWaiter({
  connection,
  signedTransaction,
  pollInterval = 500,
  sendInterval = 2_000,
  sendRetries = 40,
}: TransactionSenderAndConfirmationWaiterArgs): Promise<{
  txid: TransactionSignature;
  transactionResponse: VersionedTransactionResponse | null;
}> {
  const rawTransaction = signedTransaction.serialize();
  const { blockhash } = getTransactionBlockhashAndFeePayer(signedTransaction);
  const txid = await connection.sendRawTransaction(rawTransaction, SEND_OPTIONS);
  let lastSendTimestamp = getUnixTs();
  let retries = 0;

  while (true) {
    const timestamp = getUnixTs();
    if (retries < sendRetries && timestamp - lastSendTimestamp > sendInterval) {
      lastSendTimestamp = timestamp;
      retries += 1;
      await connection.sendRawTransaction(rawTransaction, SEND_OPTIONS);
    }
    const isBlockhashValid = await checkIsBlockhashValid({ connection, blockhash });
    const response = await Promise.any([
      connection.getTransaction(txid, {
        commitment: 'confirmed',
        maxSupportedTransactionVersion: 0,
      }),
      wait(5000),
    ]);
    if (response) return { txid, transactionResponse: response };
    if (!isBlockhashValid)
      return {
        txid,
        transactionResponse: null,
      };
    await wait(pollInterval);
  }
}

export function getSignature(transaction: Transaction | VersionedTransaction): string {
  const signature = 'signature' in transaction ? transaction.signature : transaction.signatures[0];
  if (!signature) {
    throw new Error('Transaction has no signature');
  }
  return bs58.encode(signature);
}

export function getTransactionBlockhashAndFeePayer(transaction: Transaction | VersionedTransaction): {
  blockhash: Blockhash;
  feePayer: PublicKey;
} {
  if ('message' in transaction) {
    return { feePayer: transaction.message.staticAccountKeys[0], blockhash: transaction.message.recentBlockhash };
  } else {
    if (!transaction.feePayer) throw new Error('Missing feePayer');
    if (!transaction.recentBlockhash) throw new Error('Missing blockhash');
    return { feePayer: transaction.feePayer, blockhash: transaction.recentBlockhash };
  }
}

export function signTransaction(
  transaction: Transaction | VersionedTransaction,
  signers: Signer[],
): TransactionSignature {
  if ('message' in transaction) {
    transaction.sign(signers);
  } else {
    transaction.sign(...signers);
  }
  return getSignature(transaction);
}
