import { parseErrorForTransaction, TransactionError, UNKNOWN_ERROR } from '@mercurial-finance/optimist';
import { VersionedTransactionResponse } from '@solana/web3.js';
import {
  COMMON_ERRORS,
  JUPITER_V5_ERRORS,
  JUPITER_PROGRAM_V5_ID,
  JUPITER_PROGRAM_V5_1_ID,
  JUPITER_PROGRAM_ID,
} from '../constants';

// Throws when not found or contains error to satisfy upper control flow
export function validateTransactionResponse({
  txid,
  transactionResponse,
}: {
  txid: string;
  transactionResponse: VersionedTransactionResponse | null;
}): VersionedTransactionResponse | TransactionError {
  if (!transactionResponse) {
    const errorDetails = COMMON_ERRORS['TransactionNotConfirmed'];
    return new TransactionError(errorDetails.msg, txid, errorDetails.code, [JUPITER_PROGRAM_ID.toBase58()]);
  }
  if (transactionResponse?.meta?.err) {
    let { message, programIds, code } = parseErrorForTransaction(transactionResponse);

    // only map message if first program is jupiter
    if (
      (programIds?.[0] === JUPITER_PROGRAM_V5_ID.toBase58() ||
        programIds?.[0] === JUPITER_PROGRAM_V5_1_ID.toBase58()) &&
      code
    ) {
      Object.values(JUPITER_V5_ERRORS).some((error) => {
        if (error.code === code) {
          message = error.msg;
          return true;
        }
        return;
      });
    }

    if (!message) {
      message = UNKNOWN_ERROR;
    }

    return new TransactionError(message || 'Something went wrong', txid, code, programIds);
  }
  return transactionResponse;
}
